.thirdSectionContainer {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    background: black;
}

.thirdSectionContent {
    width: 95%; /* 화면 크기에 따라 조정 가능한 넓이 */
    max-width: 1400px; /* 최대 너비를 약간 넓게 설정 */
    height: auto;
    margin: auto;
    text-align: center;
    overflow: hidden;
}

/* 행성 크기 조정 */
.planet {
    width: 250px; /* 행성 크기 확대 */
    height: 250px;
    transform: scale(1.2); /* 행성 크기를 1.2배로 확대 */
}

/* .logo 스타일 */
.thirdSectionContainer .logo {
    color: white;
    position: absolute;
    top: 20px; /* 글자가 더 위로 오도록 조정 */
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: 4px;
}

/* .logo의 하위 span 스타일 */
.thirdSectionContainer .logo span {
    font-size: 12px;
    color: #f39041;
    display: block;
}

/* 버튼 스타일 */
.thirdSectionButton {
    position: absolute;
    bottom: 40px; /* 버튼이 화면 바닥에서 더 올라오도록 조정 */
    left: 50%;
    transform: translateX(-50%);
}

/* 반응형 디자인으로 행성 및 텍스트 크기 조정 */
@media (max-width: 768px) {
    .planet {
        width: 200px;
        height: 200px;
        transform: scale(1); /* 작은 화면에서는 기본 크기로 유지 */
    }
    .thirdSectionButton {
        width: 40px; /* 작은 화면에서 버튼 크기 축소 */
        height: 40px;
    }
}